import { useState, useEffect, useRef } from 'react';
import { useParams } from 'react-router-dom';
import { useTheme } from '@mui/material/styles';

// @mui
import {
  Box,
  Grid,
  Card,
  Divider,
  Container,
  Typography,
  TableContainer
} from '@mui/material';
import ReactToPrint from 'react-to-print';
import { LoadingButton } from '@mui/lab';
// components
import Iconify from '../../components/Iconify';
import Image from '../../components/Image';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
import Scrollbar from '../../components/Scrollbar';
// utils
import { fCurrency } from '../../utils/formatNumber';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import Label from '../../components/Label';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
// sections
import axios from '../../utils/axios';
import useAuth from '../../hooks/useAuth';
import useResponsive from '../../hooks/useResponsive';
import styles from './style.css';
import { APP_NAME } from '../../config';

import { InvoiceToolbar } from '../../sections/@dashboard/invioce/recharge_habukhan_success';


// ----------------------------------------------------------------------


export default function RechargeCardPrint() {
  const { themeStretch } = useSettings();
  const { name = '' } = useParams();
  const [trans, Settrans] = useState('');
  const { setting } = useAuth();
  const [load, SetLoad] = useState(true);
  const [map, setMap] = useState([])
  const isDesktop = useResponsive('up', 'sm');
  const theme = useTheme();

  const DataCardPrint = useRef();


  useEffect(() => {
    axios.get(`/api/recharge_card/trans/${name}/secure/sucess`).then(data => {
      Settrans(data.data?.trans);
      setMap(data?.data?.card_map);
      SetLoad(false);
    }).catch(() => SetLoad(false));
  }, [name, Settrans]);

  return (
    <Page title="Recharge Card : Printing">
      <Container maxWidth={themeStretch ? false : 'lg'} sx={{ px: { xs: 2, md: 5 } }}>
        <HeaderBreadcrumbs
          heading="Recharge Card Printing"
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.root },
            { name: 'Recharge Card Printing' },
          ]}
        />

        {trans && <InvoiceToolbar invoice={trans} MapData={map} />}

        <Card sx={{ pt: 5, px: 5 }}>
          <Grid container>
            <Grid item xs={12} sm={6} sx={{ mb: 5 }}>
              <Image disabledEffect visibleByDefault alt="logo" src="/upload/welcome.png" sx={{ maxWidth: 120 }} />
            </Grid>

            <Grid item xs={12} sm={6} sx={{ mb: 5 }}>
              <Box sx={{ textAlign: { sm: 'right' } }}>
                <Label color={trans?.plan_status === 1 ? 'success' : (trans?.plan_status === 2 ? 'error' : 'warning')} sx={{ textTransform: 'uppercase', mb: 1 }}>
                  {trans?.plan_status === 1 && 'successful' || trans?.plan_status === 0 && 'proccessing' || trans?.plan_status === 2 && 'failed'}
                </Label>
                <Typography variant="h6">{trans?.transid}</Typography>
              </Box>
            </Grid>
          </Grid>

          {load && <Typography variant="h6" style={{ "text-align": "center" }}>Please Wait ....</Typography>}

          <Box sx={{ mt: 3 }}>
            <Grid container spacing={3}>
              {map.map((card, index) => (
                <Grid item xs={12} sm={6} md={4} key={index}>
                  <Card sx={{ p: 3, border: '1px solid #eee', boxShadow: '0 2px 10px rgba(0,0,0,0.05)' }}>
                    <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
                      <Typography variant="subtitle2" sx={{ fontSize: 16, fontWeight: 'bold' }}>
                        {trans?.card_name}
                      </Typography>
                      <Image
                        src={
                          (trans?.network === 'MTN' && '/upload/mtn.png?v=2') ||
                          (trans?.network === 'AIRTEL' && '/upload/airtel.png?v=2') ||
                          (trans?.network === 'GLO' && '/upload/glo.png?v=2') ||
                          (trans?.network === '9MOBILE' && '/upload/mobile.png?v=2')
                        }
                        sx={{ width: 30, height: 30, borderRadius: '50%' }}
                      />
                    </Box>
                    <Typography variant="h6" sx={{ color: 'primary.main', mb: 2 }}>
                      {fCurrency(trans?.plan_name)}
                    </Typography>

                    <Box sx={{ bgcolor: 'background.neutral', p: 2, borderRadius: 1, textAlign: 'center', mb: 2 }}>
                      <Typography variant="caption" sx={{ color: 'text.secondary', display: 'block', mb: 0.5 }}>PIN</Typography>
                      <Typography variant="h5" sx={{ letterSpacing: 2, fontWeight: 'bold' }}>
                        {card?.pin}
                      </Typography>
                    </Box>

                    <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 2 }}>
                      <Typography variant="body2" sx={{ color: 'text.secondary' }}>Serial:</Typography>
                      <Typography variant="body2" sx={{ fontWeight: 'bold' }}>{card?.serial}</Typography>
                    </Box>

                    <Divider sx={{ my: 2 }} />

                    <Typography variant="caption" sx={{ display: 'block', color: 'text.secondary', textAlign: 'center' }}>
                      Load: {trans?.load_pin}{card?.pin}#
                    </Typography>
                    <Typography variant="caption" sx={{ display: 'block', color: 'text.secondary', textAlign: 'center' }}>
                      Bal: {trans?.check_balance}
                    </Typography>
                  </Card>
                </Grid>
              ))}
            </Grid>
          </Box>






          <Divider sx={{ mt: 5 }} />

          <Grid container>
            <Grid item xs={12} md={9} sx={{ py: 3 }}>
              <Typography variant="subtitle2">NOTES</Typography>
              <Typography variant="body2">
                We appreciate your continuous patronage. Your satisfaction is our top priority!
              </Typography>
            </Grid>
            <Grid item xs={12} md={3} sx={{ py: 3, textAlign: 'right' }}>
              <Typography variant="subtitle2">any enquiry, kindly contact us @</Typography>
              <Typography variant="body2">{setting?.general?.app_email}</Typography>
            </Grid>
          </Grid>
        </Card>
      </Container>
    </Page>
  );
}
